@import url("https://fonts.googleapis.com/css?family=Merriweather");
@import url("https://fonts.googleapis.com/css?family=Lato");
.home-page {
  /*MAIN ANNIMATION SECTION*/
  font-family: 'Lato';
  /*END MAIN ANNIMATION SECTION*/
  font-family: lato;
}

.home-page .fixed-indicator {
  position: fixed;
  top: 100px;
  height: 1px;
  width: 3px;
  background: black;
  z-index: 3;
}

.home-page .main-annimation {
  height: 50vw;
  padding: 25px 0;
  display: table;
  width: 100%;
}

.home-page .main-annimation .first-animation {
  margin-top: 30px;
}

.home-page .main-annimation .first-animation .left {
  display: table-cell;
  width: 50vw;
  height: 100%;
}

.home-page .main-annimation .first-animation .left .maps-container {
  position: relative;
  left: 8vw;
}

.home-page .main-annimation .first-animation .left .maps-container img {
  width: 60%;
}

.home-page .main-annimation .first-animation .left .maps-container .africa-map {
  position: absolute;
  top: 100px;
}

.home-page .main-annimation .first-animation .left .maps-container .africa-shadow {
  opacity: 0;
  position: absolute;
  top: 30px;
  left: 30px;
  z-index: -2;
}

.home-page .main-annimation .first-animation .right {
  /*
        display: table-cell;
        */
  width: 50vw;
  height: 100%;
}

.home-page .main-annimation .first-animation .right .annim {
  height: 40vw;
  position: absolute;
  right: -460px;
  background: url("/img/annimation/right-1.png") no-repeat;
  background-size: contain;
  background-position: right;
}

.home-page .main-annimation .first-animation .right .annim .anim-text {
  background: white;
  top: 6vw;
  right: 6vw;
  height: 25vw;
  display: block;
  width: 30vw;
  position: relative;
}

.home-page .main-annimation .first-animation .right .annim .anim-text p {
  font-size: 2vw;
}

.home-page .main-annimation .first-animation .right .annim .anim-text span {
  font-size: 7vw;
}

.home-page .video-conainer {
  width: 100%;
}

.home-page .video-conainer video {
  width: 100%;
}

.home-page .video-conainer .text-video {
  position: absolute;
  top: 30vw;
  padding: 0 8%;
  margin: 0 auto;
  color: white;
}

.home-page .video-conainer .text-video h2 {
  font-family: 'Merriweather';
  font-size: 28px;
}

.home-page .video-conainer .text-video h4 {
  font-weight: bold;
}

@media (max-width: 767px) {
  .home-page .video-conainer .text-video {
    position: absolute;
    top: 10vw;
  }
}

.home-page .video-conainer .text-video .right-text-video {
  position: absolute;
  bottom: 0;
  right: 50px;
}

@media (max-width: 767px) {
  .home-page .video-conainer .text-video .right-text-video {
    position: relative;
  }
}

.home-page .video-conainer .text-video .right-text-video .goal-container {
  width: 100%;
  height: 15px;
  border-radius: 15px;
  border: solid 1px white;
}

.home-page .video-conainer .text-video .right-text-video .goal-container .goal-bar {
  max-width: 100%;
  width: 0%;
  background: white;
  height: 100%;
  border-radius: 15px;
}

.home-page .video-conainer .text-video .right-text-video .goal-container .goal-button {
  border: 1px solid white;
  background: transparent;
  color: white;
  float: right;
  margin-top: 20px;
}

@media (max-width: 767px) {
  .home-page .video-conainer .text-video .right-text-video .goal-container .goal-button {
    float: none;
    margin: 20px auto;
    width: 60%;
    display: block;
  }
}

.home-page .video-conainer .text-video .right-text-video .goal-container .goal-button:hover {
  background: rgba(173, 173, 173, 0.6);
}

.home-page .para {
  color: blue;
}

@media (max-width: 767px) {
  .home-page .para {
    color: green;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .home-page .para {
    color: red;
  }
}

.home-page .logo-home {
  position: absolute;
  top: 50px;
  left: 30px;
}

.home-page .news {
  background-color: #F9F9F9;
  padding: 50px 0;
}

.home-page .news h1 {
  margin-left: 170px;
  margin-bottom: 50px;
  font-family: Merriweather;
}

.home-page .news .main-new {
  background-color: white;
  margin: 10px 70px;
  font-family: lato;
  height: 300px;
  box-shadow: 0 2px 2px lightgrey;
  display: table;
}

.home-page .news .main-new .left {
  width: 50%;
  height: 100%;
  display: table-cell;
  padding: 50px 70px;
}

.home-page .news .main-new .left h1 {
  width: 700px;
  margin-left: initial;
  margin-bottom: initial;
}

.home-page .news .main-new .left h4 {
  width: 700px;
  margin: 20px 0px;
}

.home-page .news .main-new .left p {
  width: 700px;
}

.home-page .news .main-new .right {
  display: table-cell;
  width: 50%;
  height: 100%;
}

.home-page .news .main-new .right .main-image {
  background-image: url("/img/favicon.png");
  width: 100%;
  height: 100%;
}
